@import "../../styles/styles.scss";

$color_white: #fff;
$color_blue: $color__primary;
$color_light-grey: #f6f6f6;
$color_grey: #2f4356;

.FAQ {
    .hero {
        position: relative; 
        padding-top: rem(100);
        //background: linear-gradient(125.24deg, $color_blue 27.35%, #f2f9ff 176.96%);
        background: linear-gradient(125.24deg, #0A61B4 100%, #074581 100%);
        &__wrapper {
            display: flex; 
            align-items: center;
            padding-top: rem(40);
            padding-bottom: rem(80);
            text-align: center;
            box-sizing: border-box;
        }
        &__content {
            color: $color_white;
            flex: 1 1 40%;
        }
        &__title {
            font-size: rem(24);
        }
        @include minMedium {
            padding-top: rem(60);
            padding-bottom: rem(60);
        }
        @include minLarge {
            padding-top: rem(80);
            padding-bottom: rem(100);
        }
    }
    .content {
        &__wrapper {
            @include minMedium {
                display: grid;
                grid-template-columns: 1fr 3fr;
            }
            @include minLarge {
                margin-top: rem(100);
                display: grid; 
                grid-template-columns: 3fr 1fr;
            }
        }
        .table-wrapper {
            padding: 0 1rem; 
            padding-right: 0;
            display: none; 
            @include minMedium {
                display: block; 
                grid-column: 1;
            }
            &-title{
                padding: 0 1rem;
                align-items: left; 
            }
        }
        .side-table {
            display: none; 
            @include minMedium {
                display: inline-block;
        width: 100%;
        break-inside: avoid-column;
        ul {
            padding: 0;
            list-style: none;
            display: flex;
            flex-direction: column;
            gap: rem(12);

            li {
                cursor: pointer;
                font-weight: 600px;
                font-size: rem(14);
                line-height: 25px; 
                letter-spacing: 0.005em;
                
                &:hover {
                    color: #2D4BB6;
                }
            }
        }   
        border-radius: 20px;
            }
        }
        h1 {
            font-size: 16px;
        }
        p {
            padding: 1rem 0;
            font-size: 14px;
            letter-spacing: 0.005em;
            line-height: 22px;
        }
        &__wrapper {
            padding: 0 1rem;
        }
    }
    .black-bar {
        margin: 0 1rem;
        border: none;
        height: 1px; 
        background-color: rgba(47, 67, 86, 0.1);
    
        @include minSmall {
            //display: none; 
        }
        @include minMedium {
            background-color: white;
        }
        @include minLarge {
            background-color: #BACCDE;
        }
    }

    .article {
        @include minLarge {
            display: none; 
        }
        p {
            font-size: rem(16);
            line-height: 25px;
            letter-spacing: 0.005em;
        }
        @include minMedium {
            grid-column: 2;
            grid-row: 1 / 6;
        }
        @include minLarge {
            grid-column: 1;
        }
        padding: 1rem; 
        
    }
    .meow {
        display: none;
        @include minLarge {
            display: block; 
            grid-column: 2;
            grid-row: 1 / 6;
            ul {
                padding: 0;
                list-style: none;
                display: flex;
                flex-direction: column;
                gap: rem(12);
    
                li {
                    background-color: white;
                    //display: flex;
                    cursor: pointer;
                    //font-family: "Gotham Rounded";
                    //font-style: "normal";
                    font-weight: 600px;
                    font-size: rem(14);
                    line-height: 25px; 
                    letter-spacing: 0.005em;
                    //width: 100%;
                    
                    &:hover {
                        color: #2D4BB6;
                        background-color: white;
                    }
                }
            }  
        }
    }
    .whatever {
        display: flex;
    }

    .dropbtn {
        background-color: white;
        color: black;
        width:100%;
        padding: 16px; 
        font-size: rem(14); 
        border: none;
        border-radius: 10px;
        display: flex;
        justify-content: space-between;
        position: relative;
    
        cursor: pointer; 
        @include minMedium {
           display: none;
        }
        img {
            align-self: center;
        }
        .blue-bar {
            position: absolute;
            left: 0;
            top: 50%;
            z-index: 11000;
            transform: translateY(-50%);
        } 
        &:hover {
            color: #0a61b4;
             .blue-bar {
                background-color: #0A61B4;
            } 
        }
    }
    .whatever{
        display: flex;
    }
    
    .table-wrapper {
        padding: 0 1rem; 
        position: relative;
    }
    .dropdown-table {
        display: none;
        @include minMedium {
            display: inline-block;
            width: 100%;
            break-inside: avoid-column;
            ul {
                padding: 0 1rem;
                list-style: none;
                display: flex;
                flex-direction: column;
                gap: rem(8);
    
                li {
                    cursor: pointer;
                    font-weight: 350px;
                    font-size: 16px;
                    
                    &:hover {
                        color: #0a61b4;
                    }
                }
            }   
            background: #F6F6F6;
            border-radius: 20px;
        }
    }
    .dropdown {
        position: relative; 
        display: inline-block;
        width: 100%;
        @include minMedium {
            display: none;
        }
    }
    .dropdown-content  {
        display: none; 
        position: abosolute;
         background-color: #F6F6F6; 
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        z-index: 1;
        font-size: rem(14);
    }
    .dropdown-content a {
        padding: 12px 16px; 
    }
    a {
        color: black; 
        text-decoration: none;
        display: block;
        width: 100;
    }
    a:hover {
        background-color: #f1f1f1;
        color: #0A61B4;
        width: 100%;
    }
    .dropdown:hover .dropdown-content {
        display:block;
        width: 100%;
    }
    .list-item {
        display: flex; 
        a {
            width: 100%;
        }
        &:hover {
        background-color: #f1f1f1;
            .blue-bar {
                background-color: #0A61B4;
            }
    
        }
    }
    .list-item-text {
        padding-left: 10px;
        align-self: center;
    }
    .blue-bar {
        width: 2px; 
        height: 40px; 
        background-color: #F6F6F6;
    }
 }

 .information { 
    display: none;
    @include minLarge {
    display: block; 
    position: relative;
    grid-row: 1; 
    padding-bottom: rem(140);
    &__wrapper {
        padding: 0 rem(100); 
        & h1 {
            font-size: rem(38);
            
        }
        .heading {
            padding-top: 2rem; 
        }
        & table {
            width: 100%;
            margin: 0 auto; 
            tr {
				th {
					display: block;
					position: relative;
					width: 100%;
					padding: rem(20) rem(20) rem(20) 0;
					border-bottom: rem(1) solid #baccde;
					font-size: rem(18);  
					text-align: left;
					box-sizing: border-box;
					cursor: pointer;
					&::before {
                        color: pink; 
						display: inline-block;
						position: absolute;
						top: 40%;
						right: 0;
						width: rem(15);
						height: rem(15);
						background: url("../../assets/images/ico-faq-arrow.svg") center
							no-repeat;
						background-size: 100%;
						transform: rotate(-180deg);
						transition: 0.1s all ease-in;
						content: "";
					}
				}
				td {
					display: none;
					width: 100%;
					padding-bottom: rem(20);
					border-bottom: rem(1) solid #baccde;
					font-size: rem(16);
					line-height: rem(25);
					color: $color_grey;
					transition: 0.7s all ease-in;
				}
			}
			tr.on {
				th {
					border-bottom: none;
					&::before {
						transform: rotate(0deg);
					}
				}
				td {
					display: block;
				}
			}
        }
    }
}
}